document.addEventListener('DOMContentLoaded', () => {

    const toggleButton = document.getElementById('menu-toggle-button');
    const sidebar = document.querySelector('.fixed-sidebar');

    if (toggleButton && sidebar) {
        // La fonction de bascule
        function toggleMenu() {
            // Bascule la classe 'is-open' sur le menu
            const isMenuOpen = sidebar.classList.toggle('is-open');

            // Met à jour l'attribut ARIA pour l'accessibilité
            toggleButton.setAttribute('aria-expanded', isMenuOpen);

            // Optionnel : Changer l'icône du bouton (ex: X à la place de ☰)
            toggleButton.textContent = isMenuOpen ? '✕' : '☰';
        }

        // Événement au clic
        toggleButton.addEventListener('click', toggleMenu);

        // OPTIONNEL : Fermer le menu si l'utilisateur clique sur un lien (sur mobile)
        const menuLinks = sidebar.querySelectorAll('a');
        menuLinks.forEach(link => {
            link.addEventListener('click', () => {
                // Vérifie si le menu est actuellement ouvert (classe 'is-open')
                if (sidebar.classList.contains('is-open')) {
                    toggleMenu(); // Ferme le menu
                }
            });
        });

    } else {
        console.error("Éléments de bascule du menu (bouton ou barre latérale) introuvables.");
    }



})
